import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs/observable';
import { map } from 'rxjs/operators';
import { Http } from '@angular/http';

import { ToDoList } from '../models/to-do-list.model';
import { Configuration } from '../app.constants';

@Injectable()
export class ToDoListService {
    private actionUrl: string;

    constructor(private httpClient: Http, private _configuration: Configuration) {
        this.actionUrl = _configuration.ServerWithApiUrl;
    }

    getToDoLists(): Observable<any[]> {
        return this.httpClient.get(this.actionUrl + 'ToDoList').pipe(map(data => {
            let res = data.json();
            return res || [];
        }));
        // let dataArray = [{ id: 1, text: 'Hit the gym', isChecked: false }, { id: 2, text: 'Pay bills', isChecked: true },
        // { id: 3, text: 'Meet George', isChecked: false }, { id: 4, text: 'Buy eggs', isChecked: false },
        // { id: 5, text: 'Read a book', isChecked: false }, { id: 6, text: 'Organize office', isChecked: false }];
        // return Observable.create(observer => {
        //     observer.next(dataArray);
        //     observer.complete();
        // });
    }
}


