import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {
  MatIconModule, MatToolbarModule, MatSidenavModule, MatListModule, MatFormFieldModule, MatInputModule,
  MatButtonModule, MatButtonToggleModule,MatExpansionModule
} from '@angular/material';
import { MediaMatcher } from '@angular/cdk/layout';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from '@angular/router';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';

import { routing, appRoutingProviders } from './app.routing';
import { Configuration } from './app.constants'
import { AppComponent } from './component/app.component';
import { NavMenuComponent } from './component/nav-menu/nav-menu.component';
import { ToDoListComponent } from './component/to-do-list/to-do-list.component';
import { ProfileComponent } from './component/profile/profile.component';
import { SettingsComponent } from './component/settings/settings.component';
import { AddProductComponent } from './component/products/add-product.component'
import { UpdateProductComponent } from './component/products/update-product.component'

import { ToDoListService } from './services/to-do-list.service';

@NgModule({
  declarations: [
    AppComponent,
    NavMenuComponent,
    ToDoListComponent,
    ProfileComponent,
    SettingsComponent,
    AddProductComponent,
    UpdateProductComponent
  ],
  imports: [
    HttpModule,
    BrowserModule,
    MatIconModule,
    MatToolbarModule,
    MatSidenavModule,
    MatListModule,
    BrowserAnimationsModule,
    RouterModule,
    routing,
    MatFormFieldModule,
    MatInputModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatExpansionModule,
    FormsModule
  ],
  providers: [
    Configuration,
    MediaMatcher,
    appRoutingProviders,
    ToDoListService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



