import { MediaMatcher } from '@angular/cdk/layout';
import { ChangeDetectorRef, Component, OnInit, OnDestroy } from '@angular/core';
import { NavMenu, NavMenuContent } from '../../models/nav-menu.model';

@Component({
  selector: 'nav-menu',
  templateUrl: './nav-menu.component.html',
  styleUrls: ['./nav-menu.component.css']
})
export class NavMenuComponent implements OnInit, OnDestroy {
  mobileQuery: MediaQueryList;
  menuItems: NavMenu[] = [];

  fillerNav = Array(50).fill(0).map((_, i) => `Nav Item ${i + 1}`);

  // fillerContent = Array(50).fill(0).map(() =>
  //     `Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut
  //      labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
  //      laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in
  //      voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat
  //      cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.`);

  private _mobileQueryListener: () => void;

  constructor(changeDetectorRef: ChangeDetectorRef, media: MediaMatcher) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnInit() {
    let navMenuContents: NavMenuContent[] = [];
    navMenuContents.push(new NavMenuContent(1, 'Add Product', '', 'add-product'));
    navMenuContents.push(new NavMenuContent(1, 'Update Product', '', 'update-product'));
    navMenuContents.push(new NavMenuContent(3, 'Settings', '', 'settings'));
    this.menuItems.push(new NavMenu('Products', navMenuContents));

    navMenuContents = [];
    navMenuContents.push(new NavMenuContent(1, 'Sell product', '', 'sell-product'));
    navMenuContents.push(new NavMenuContent(1, 'Return Product', '', 'return-product'));
    navMenuContents.push(new NavMenuContent(3, 'Follow Up', '', 'follow-up'));
    this.menuItems.push(new NavMenu('Sales', navMenuContents));

    // // this.menuItems.push(new NavMenu(1,'Profile','','profile'));
    // // this.menuItems.push(new NavMenu(2,'To-Do-List','','to-do-list'));
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }
  // shouldRun = [/(^|\.)plnkr\.co$/, /(^|\.)stackblitz\.io$/].some(h => h.test(window.location.host));

}


