export class NavMenuContent {
    menuId: number;
    menuName: string;
    icon: string;
    pageSource: string;

    constructor(menuId: number, menuName: string, icon: string, pageSource: string) {
        this.menuId = menuId;
        this.menuName = menuName;
        this.icon = icon;
        this.pageSource = pageSource;
    }
}

export class NavMenu {
    mainMenuName: string;
    navMenuContent: NavMenuContent[];

    constructor(mainMenuName: string, navMenuContent: NavMenuContent[]) {
        this.mainMenuName = mainMenuName;
        this.navMenuContent = navMenuContent;
    }
}


