import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ToDoListComponent } from './component/to-do-list/to-do-list.component';
import { ProfileComponent } from './component/profile/profile.component';
import { SettingsComponent } from './component/settings/settings.component';
import { AddProductComponent } from './component/products/add-product.component'
import { UpdateProductComponent } from './component/products/update-product.component'

const appRoutes: Routes = [
    { path: '', redirectTo: 'to-do-list', pathMatch: 'full' },
    { path: 'to-do-list', component: ToDoListComponent },
    { path: 'profile', component: ProfileComponent },
    { path: 'settings', component: SettingsComponent },
    { path: 'add-product', component: AddProductComponent },
    { path: 'update-product', component: UpdateProductComponent }]

export const appRoutingProviders: any[] = [];
    
export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);


