import { Component, OnInit, OnDestroy } from '@angular/core';
import { ToDoList } from '../../models/to-do-list.model';
import { ToDoListService } from '../../services/to-do-list.service';

@Component({
  selector: 'to-do-list',
  templateUrl: './to-do-list.component.html',
  styleUrls: ['./to-do-list.component.css']
})

export class ToDoListComponent implements OnInit, OnDestroy {
  private title: string = 'To-Do-List';
  private listItems: ToDoList[];
  private toDoListText: string = "";

  constructor(private toDoListSvc: ToDoListService) {

  }

  ngOnInit() {
    this.getInitialData();
  }

  getInitialData() {
    this.toDoListSvc.getToDoLists().subscribe(res => {
      if (res && res.length) {
        this.listItems = res.map((data) => {
          let _tl = new ToDoList();
          _tl.fillFromJson(data);
          return _tl;
        });
      }
    }, error => {
      console.error(error);
    }, () => { });
  }

  itemClick(item: ToDoList) {
    item.isChecked = !item.isChecked;
  }

  itemDelete(item: ToDoList) {
    alert("Are you sure, You want to delete the item " + item.text + "?");
  }
  // Create a new list item when clicking on the "Add" buttonng serve --o
  
  newElement() {
    if (this.toDoListText === '') {
      alert("You must write something!");
    } else {
      let newItem = new ToDoList();
      newItem.text = this.toDoListText;
      newItem.isChecked = false;
      this.listItems.push(newItem);
      this.toDoListText = "";
    }
  }

  ngOnDestroy() {

  }
}


